.App {
  max-width: 600px;
  margin: 30px auto;
}

body {
  color: #2459eb;
  font-family: Arial, Helvetica, sans-serif;
}

h1 {
  font-family: "Bebas Neue", cursive;
  font-size: 2.5rem;
}

a {
  text-decoration: none;
  color: #7696dd;
}

a:hover {
  color: #7696dd;
}

.container {
  margin: 20px auto;
  max-width: 1000px;
}
ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;

  color: #2459eb;
  font-size: 16px;
  font-weight: 100;
  line-height: 19px;
}

li:first-letter {
  text-transform: capitalize;
}
